<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow pagination__link--disabled" aria-label="Предыдущая страница" v-if="page === 1">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
      <a class="pagination__link pagination__link--arrow" href="#" aria-label="Предыдущая страница" v-else @click.prevent="prevPage(page, pages)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" style="cursor: pointer;" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
        {{pageNumber}}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница" @click.prevent="nextPage(page, pages)">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page)
    },
    nextPage(page, pages) {
      if (page < pages) {
        let nextPage = page + 1;
        this.$emit('paginate', nextPage)
      }
    },
    prevPage(page, pages) {
      console.log(page)
      console.log(pages)
      if (page >= pages) {
        let prevPage = page - 1;
        this.$emit('paginate', prevPage)
      }
    }
  }
}
</script>
